/* SearchResultsPage.css */
.search-results-page {
    padding-top: 80px;
   }
   
   .product-cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: 20px;
   }
   
   .product-card {
    background-color: #f8f9fa;
    border-radius: 10px;
    padding: 10px;
    width: 250px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
   }
   
   .product-image {
    height: 190px;
    width: 250px;
    object-fit: contain;
    border-radius: 10px;
   }
   
   .product-details a, .product-card a {
    text-decoration: none;
    margin-top: 10px;
    color: black;
   }
   
   .he3 {
    margin-top: -10px;
   }
   
   .he3 a, .product-card a {
    font-weight: 450;
    text-decoration: none;
    color: black;
    margin-top: 0px;
   }
   
   .product-details a:hover, .he3 a:hover, .product-card a:hover {
    color: grey;
   }
   