/* SportsCategories.css */
.sports-categories {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  justify-content: center;
  }
  
  .category-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fff;
  padding: 15px;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease-in-out;
  width: 150px; 
  margin-bottom: 20px;
  }
  
.category-section:hover {
   transform: translateY(-5px);
 }
  h1, h2 {
   font-size: 25px;
   font-weight: bold;
   color: #3498db;
   text-align: center;
   margin-bottom: 20px;
  }
  
  ul {
   font-size: 20px; 
   color: #555;
  }
  
  li {
   margin-bottom: 5px;
  }
  .category img {
    width: 180px; 
    height: 180px;
   
  }
  .sports-categories a {
    text-decoration: none;
    color: black; 
    transition: color 0.3s ease;  
  }
    
  .sports-categories a:hover, .sports-categories a:active {
      color: blue; 
  }